nav
{
    background: var(--color-black-primary);
}
.logo span
{
    color: var(--color-gray-primary);
    font-family: 'Times New Roman', Times, serif;
    font-size: 16px;
}
.menu ul li
{
    border-left: 1px solid #2e333e;
}
.menu ul li:hover
{
    cursor: pointer;
    background: #111318;
}
.menu ul li a
{
    color: var(--color-white-primary);
}
.menu ul li:nth-child(1) a::before
{
    content:"";
    margin: 2px;
    display: inline-block;
    background: url(../../images/nav-icons.png) no-repeat 0 -1px;
	width: 19px;
	height: 18px;
}
.menu ul li:nth-child(2) a::before
{
    content:"";
    margin: 2px;
    display: inline-block;
    background: url(../../images/nav-icons.png) no-repeat -24px -1px;
	width: 19px;
	height: 18px;
}
.menu ul li:nth-child(3) a::before
{
    content:"";
    margin: 2px;
    display: inline-block;
    background: url(../../images/nav-icons.png) no-repeat -47px -1px;
	width: 14px;
	height: 17px;
}