.featured
{
    margin: 50px 100px 50px 0px!important;
}
.featured span:nth-child(1)
{
    color: var(--color-black-primary);
    font-family: proximanova-regular;
    font-size: 16px;
}
.featured span:nth-child(2)
{
    color: var(--color-gray-primary);
    font-family: cloisterblack-light;
    font-size: 36px;
}
.featured span:nth-child(3)
{
    color: var(--color-gray-primary);
    font-family: sniglet-regular;
    font-size: 36px;
    font-weight: bold;
}
.featured span:nth-child(4)
{
    color: var(--color-gray-primary);
    font-family: opensans-light;
    font-size: 36px;
}
.featured span:nth-child(5)
{
    color: var(--color-gray-primary);
    font-family: bemio;
    font-size: 36px;
}
.featured span:nth-child(6)
{
    color: var(--color-gray-primary);
    font-family: proximanova-regular;
    font-size: 36px;
}
.featured span a:hover
{
    cursor: pointer;
    color: #454e5c !important;
}