.content
{
    background: url(../../images/bg-hero.jpg);
    height: 75vh;
    background-repeat: no-repeat;
    background-size: cover;
    color: var(--color-white-primary);
}
.content .content-1 h1
{
    font-family: opensans-bold;
    font-size: 52px;
}
.content .content-1 p
{
    font-family: opensans-regular;
    font-size: 22px;
    width: 55%;
}
.content button
{
    font-family: opensans-semibold;
    font-size: 18px;
}
.content button:nth-child(1)
{
    background-color: var(--color-green-primary);
    height: 55px;
    width: 233px;
    border-radius: 5px;
    color: var(--color-white-primary);
    border: 1px solid transparent;
}
.content button:nth-child(1):hover
{
    background-color: greenyellow;
}
.content button:nth-child(2)
{
    background-color: var(--color-blue-primary);
    height: 55px;
    width: 176px;
    border-radius: 5px;
    color: var(--color-white-primary);
    border: 1px solid transparent;
}
.content button:nth-child(2):hover
{
    background-color: lightblue;
}
.content button:nth-child(1):focus
{
    outline: transparent;
}
.content button:nth-child(2):focus
{
    outline: transparent;
}
.content-2
{
    display: none;
}